*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;

}
.mainflex{
    display: flex;
    align-items: center;
    width: 100%;
    background-color: #719ac88b
}
.mainleft,.mainright{
    width: 50%;
    height: 900px;
    background-color: #719ac88b ;
    
}
.mainleft h1{

    font-size: 70px;
    display: flex;
    text-align: center;
    line-height:110px;
    padding: 20px;
    margin-top: 200px;
}
.mainright{
    display: flex;
    justify-content: center;
    border-left: solid 1px;
    
}
.rightbottom{
    margin-top: 140px;
    padding: 10px;
    
}
.rightbottom h1{
    text-align: center;
    padding: 20px;
    margin-top: -30px;
}
.username,.password{
    padding: 20px;
    text-align: center;
    font-size: 25px;
    
}
.username{
    margin-top: 80px;
}
.mainright button{
    margin-left: 220px;
    margin-top: 50px;
    padding: 5px 25px;
}
